import React, { useState } from "react";
import {
  Modal,
  Backdrop,
  Fade,
  CardContent,
  Card,
  Typography,
  TextField,
  Divider,
} from "@material-ui/core";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import useStyles from "../../styles";

import AddCircleOutlineOutlinedIcon from "@material-ui/icons/AddCircleOutlineOutlined";
import IndeterminateCheckBoxOutlinedIcon from "@material-ui/icons/IndeterminateCheckBoxOutlined";

export default function ModalProcess(props) {
  const classes = useStyles();
  const { open, handleModal, index } = props;
  const [todo, setTodo] = useState([
    {
      todo: "",
    },
  ]);

  const addTodo = () => {
    setTodo([...todo, { todo: "" }]);
  };

  const handleChange = (i) => (e) => {
    const newTodo = [...todo].map((item, index) => {
      if (i === index) {
        return { todo: e.target.value };
      }
      return item;
    });
    setTodo(newTodo);
  };

  const deleteTodo = (i) => () => {
    const newTodo = [...todo].filter((item, index) => i !== index);
    setTodo(newTodo);
  };

  return (
    <Modal
      className={classes.modal}
      open={open}
      closeAfterTransition
      BackdropComponent={Backdrop}
    >
      <Fade in={open}>
        <div>
          <Card className={classes.card} variant="outlined">
            <CardContent>
              <div className={classes.close}>
                <HighlightOffIcon
                  fontSize="large"
                  className="close-icon"
                  onClick={() => handleModal({ index, label: "process" })}
                />
              </div>

              <div className={classes.containerevent}>
                <div className={classes.modalheader}>
                  <Typography>
                    <b>Setup requirements </b>
                  </Typography>
                  <Typography>
                    <b>step proses</b>
                  </Typography>
                </div>

                <div>Add process</div>
                <div className={classes.todo}>
                  {[...todo].map((item, i) => (
                    <>
                      <TextField
                        className="input-todo"
                        value={todo[i].todo}
                        onChange={handleChange(i)}
                        key={i}
                        variant="outlined"
                        size="small"
                      />
                      {todo.length > 1 && (
                        <IndeterminateCheckBoxOutlinedIcon
                          className="delete-todo"
                          onClick={deleteTodo(i)}
                        />
                      )}
                    </>
                  ))}
                  {todo.length <= 4 && (
                    <AddCircleOutlineOutlinedIcon
                      className="add-todo"
                      onClick={addTodo}
                    />
                  )}
                </div>

                <div className={classes.btncntrl}>
                  <div className={classes.flx1}>
                    <button className="btncancel" onClick={handleModal}>
                      Cancel
                    </button>
                    <button className="btnapply">Apply</button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </Fade>
    </Modal>
  );
}
