import { API, requestBody } from "../config";

export const getJurusan = () => {
  return async (dispatch) => {
    dispatch({
      type: "JURUSAN_PENDING",
    });
    try {
      const res = await API.post("jurusan/read", requestBody);
      dispatch({
        type: "JURUSAN_FULFILLED",
        payload: res.data.data,
      });
    } catch (err) {
      dispatch({
        type: "JURUSAN_REJECTED",
      });
    }
  };
};
