import { API, requestBody } from "../config";

export const getPelamar = (data) => {
  const newData = {
    ...data,
    ...requestBody,
  };
  console.log("ini newData", newData);
  return async (dispatch) => {
    dispatch({
      type: "PELAMAR_PENDING",
    });
    try {
      if (!data) {
        newData.status_screening = "belum";
      }
      const res = await API.post("/pelamar/read", newData);
      let newDataChip = [];
      console.log(data, "woi data untuk chip");
      for (let prop in data) {
        if (prop !== "status_screening" || data.length > 1) {
          newDataChip = [...newDataChip, { label: data[prop], name: prop }];
        }
      }
      dispatch({
        type: "PELAMAR_FULFILLED",
        payload: res.data.data,
        chip: newDataChip,
      });
    } catch (err) {
      dispatch({
        type: "PELAMAR_REJECTED",
      });
    }
  };
};

