import React, { useEffect } from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import msg from "../asset/icons/message.svg";
import bell from "../asset/icons/bell.svg";
import useStyles from "./styles";
import Loader from "react-loader-spinner";

import {
  Drawer,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  CssBaseline,
  InputBase,
  Button,
  Divider,
  Collapse,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import Logo from "../asset/icons/ocrlogos.svg";
import Menuhambuger from "../asset/icons/menu.svg";
import FaceIcon from "@material-ui/icons/Face";

import Menu2 from "./Menu";

export default function Dashboard(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(true);
  const [profile, setProfile] = React.useState(false);
  const [Loading, setLoading] = React.useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false);
    }, 1000);
    return () => clearTimeout(timer);
  }, []);

  const handleDrawer = () => {
    setOpen(!open);
  };
  const handleProfile = () => {
    setProfile(!profile);
  };
  const handleLogout = (e) => {
    e.preventDefault();
    localStorage.clear();
    window.location.reload(true);
  };

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar className={classes.toolbars}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawer}
            edge="start"
            className={clsx(classes.menuButton, open && classes.hide)}
          >
            <img src={Menuhambuger} alt="hamberger" width="30px" />
          </IconButton>

          <div className={classes.flexapp}>
            <div className={classes.flex}>
              {open === false ? (
                ""
              ) : (
                <IconButton onClick={handleDrawer}>
                  <img src={Menuhambuger} alt="hamberger" width="30px" />
                </IconButton>
              )}
              <div className={classes.flex1}>
                {/* <Typography>Dashboard</Typography> */}
                <div className={classes.search}>
                  <div className={classes.searchIcon}>
                    <SearchIcon />
                  </div>
                  <InputBase
                    placeholder="Search here"
                    classes={{
                      root: classes.inputRoot,
                      input: classes.inputInput,
                    }}
                    inputProps={{ "aria-label": "search" }}
                  />
                </div>
              </div>
            </div>
            <div className={classes.flex}>
              <div className={classes.padd1}>
                <IconButton className={classes.iconmsg} onClick={handleDrawer}>
                  <img src={msg} alt="msg" width="25px" />
                </IconButton>
              </div>
              <div className={classes.padd1}>
                <IconButton className={classes.iconbell} onClick={handleDrawer}>
                  <img src={bell} alt="bell" width="25px" />
                </IconButton>
              </div>
              <Typography className={classes.prfiletxt}>Hello, Zam</Typography>

              <IconButton
                className={classes.profile}
                variant="contained"
                onClick={handleProfile}
              >
                <FaceIcon style={{ color: "white" }} />
              </IconButton>
              <Collapse in={profile}>
                <div className={classes.logout}>
                  <Button variant="outlined" className="btnlogout">
                    Profile
                  </Button>
                  <Divider />
                  <Button
                    variant="outlined"
                    className="btnlogout"
                    onClick={handleLogout}
                  >
                    logout
                  </Button>
                  <Divider />
                </div>
              </Collapse>
            </div>
          </div>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
          <div className={classes.flex1}>
            <img src={Logo} alt="hamberger" width="190px" />
            <div> </div>
          </div>
        </div>

        <Menu2 />
      </Drawer>
      <main
        className={clsx(classes.content, {
          [classes.contentShift]: open,
        })}
      >
        <div className={classes.drawerHeader} />
        {Loading ? (
          <div className={classes.loading}>
            <Loader type="ThreeDots" color="#2F4CDD" height={80} width={80} />
          </div>
        ) : (
          <div>{props.children}</div>
        )}
      </main>
    </div>
  );
}
