import { API, requestBody } from "../config";

export const getEvent = () => {
  return async (dispatch) => {
    dispatch({
      type: "EVENT_PENDING",
    });
    try {
      const res = await API.post("/event/read", requestBody);
      dispatch({
        type: "EVENT_FULFILLED",
        payload: res.data.data,
      });
    } catch (err) {
      dispatch({
        type: "EVENT_REJECTED",
      });
    }
  };
};

export const UpdateEvent = (data) => {
  return async (dispatch) => {
    const newData = {
      ...data,
      ...requestBody,
    };
    console.log(newData, "data baru");
    try {
      console.log(data, "masukkkkk cok");
      dispatch({
        type: "UPEVENT_PENDING",
      });
      const res = await API.post("/event/update", newData);
      console.log("masuk res", res.data);
      dispatch({
        type: "UPEVENT_FULFILLED",
        payload: res.data.data,
      });
      console.log("woi harusnya ke dispatch");
    } catch (err) {
      dispatch({
        type: "UPEVENT_REJECTED",
      });
    }
  };
};
