import { API, requestBody } from "../config";

export const postOCR = (data) => {
  console.log(data, "bobi kamu kemana");
  return async (dispatch) => {
    const newData = {
      ...data,
      ...requestBody,
    };

    let response = {};
    let error = {};
    dispatch({
      type: "EVENT_PENDING",
    });
    try {
      const res = await API.post("ocr/match_ijazah", newData);
      console.log(res, "ijzah");
      response.matchIjazah = res.data.data;
    } catch (err) {
      console.log(err);
      error.matchIjazah = true;
    }
    try {
      const res = await API.post("ocr/update_match_ijazah", requestBody);
      console.log(res, "updateijazah");
      response.updateIjazah = res.data.data;
    } catch (err) {
      console.log(err);
      error.updateIjazah = true;
    }
    try {
      const res = await API.post("ocr/match_ktp", newData);
      response.matchKtp = res.data.data;
    } catch (err) {
      console.log(err);
      error.matchKtp = true;
    }
    try {
      const res = await API.post("ocr/update_match_ktp", requestBody);
      response.updateKtp = res.data.data;
    } catch (err) {
      console.log(err);
      error.updateKtp = true;
    }

    dispatch({
      type: "EVENT_FULFILLED",
      payload: { response, error },
    });
  };
};
