const initState = {
  data: [],
  authenticated: false,
  loading: true,
  error: null,
  isLogin: false,
};

const event = (state = initState, action) => {
  switch (action.type) {
    case `EVENT_PENDING`:
      return {
        ...state,
        loading: true,
      };

    case `EVENT_FULFILLED`:
      return {
        ...state,
        authenticated: true,
        data: action.payload,
        isLogin: true,
        loading: false,
        error: false,
      };

    case `EVENT_REJECTED`:
      return {
        ...state,
        loading: false,
        isLogin: false,
        error: true,
      };
    case `UPEVENT_PENDING`:
      return {
        ...state,
        loading: true,
      };
    case `UPEVENT_FULFILLED`:
      return {
        ...state,
        authenticated: true,
        isLogin: true,
        loading: false,
        error: false,
      };
    case `UPEVENT_REJECTED`:
      return {
        ...state,
        loading: false,
        isLogin: false,
        error: true,
      };
    default:
      return state;
  }
};

export default event;
