import React from "react";
import { Modal, Backdrop, Fade, Slider, Card, Button } from "@material-ui/core";

import ModalLoading from "./modalloading";
import ModalDone from "./modaldone";
import useStyles from "../../../styles";
import { postOCR } from "../../../../../../_action/ocr";
import { connect } from "react-redux";

function ModalThreshold(props) {
  const classes = useStyles();
  const [openLoading, setOpenLoading] = React.useState(false);
  const [done, setDone] = React.useState(false);
  const [progress, setProgress] = React.useState(0);
  const { open, handleThreshold, dataModal } = props;
  console.log(dataModal.id, "data bobi");
  const marks = [
    {
      value: 0,
      label: "0%",
    },
    {
      value: 25,
      label: "",
    },
    {
      value: 50,
      label: "50%",
    },
    {
      value: 75,
      label: "",
    },
    {
      value: 100,
      label: "100%",
    },
  ];

  const handleModalDone = () => {
    setDone(!done);
  };

  const valuetext = (value) => {
    return `${value}%`;
  };
  function valueLabelFormat(value) {
    return marks.findIndex((mark) => mark.value === value);
  }
  const handleLoading = async () => {
    handleThreshold();
    timer();
    setOpenLoading(!openLoading);

    const data = dataModal.id;
    await props.postOCR({ id_pelamar: data });
  };

  function timer() {
    const add = setInterval(() => {
      setProgress((oldProgress) => {
        if (oldProgress === 100) {
          clearInterval(add);
          setOpenLoading(false);
          setDone(true);
          return 0;
        }
        const diff = 1 * 1;
        return Math.min(oldProgress + diff, 100);
      });
    }, 400);
  }
  const { data } = props.ocr;

  return (
    <>
      <Modal
        className={classes.modal}
        open={open}
        closeAfterTransition
        BackdropComponent={Backdrop}
      >
        <Fade in={open}>
          <div className="containermodal">
            <Card className="card2">
              <div className="header">
                Setting nilai threshold/
                <br />
                confidence score
              </div>
              <div className="box-thres">
                <div className="flex1">Nama</div>
                <div className="flex2">ktp</div>
                <div className="flex3">
                  <Slider
                    defaultValue={25}
                    valueLabelFormat={valueLabelFormat}
                    getAriaValueText={valuetext}
                    aria-labelledby="discrete-slider-restrict"
                    step={null}
                    valueLabelDisplay="auto"
                    marks={marks}
                  />
                </div>
              </div>
              <div className="box-thres">
                <div className="flex1">NIK</div>
                <div className="flex2">ktp</div>
                <div className="flex3">
                  {" "}
                  <Slider
                    defaultValue={25}
                    valueLabelFormat={valueLabelFormat}
                    getAriaValueText={valuetext}
                    aria-labelledby="discrete-slider-restrict"
                    step={null}
                    valueLabelDisplay="auto"
                    marks={marks}
                  />
                </div>
              </div>
              <div className="box-thres">
                <div className="flex1">Tanggal lahir</div>
                <div className="flex2">ktp</div>
                <div className="flex3">
                  {" "}
                  <Slider
                    defaultValue={50}
                    valueLabelFormat={valueLabelFormat}
                    getAriaValueText={valuetext}
                    aria-labelledby="discrete-slider-restrict"
                    step={null}
                    valueLabelDisplay="auto"
                    marks={marks}
                  />
                </div>
              </div>
              <div className="box-thres">
                <div className="flex1">IPK</div>
                <div className="flex2">Ijazah</div>
                <div className="flex3">
                  {" "}
                  <Slider
                    defaultValue={25}
                    valueLabelFormat={valueLabelFormat}
                    getAriaValueText={valuetext}
                    aria-labelledby="discrete-slider-restrict"
                    step={null}
                    valueLabelDisplay="auto"
                    marks={marks}
                  />
                </div>
              </div>
              <div className="box-thres">
                <div className="flex1">Universitas</div>
                <div className="flex2">Ijazah</div>
                <div className="flex3">
                  {" "}
                  <Slider
                    defaultValue={25}
                    valueLabelFormat={valueLabelFormat}
                    getAriaValueText={valuetext}
                    aria-labelledby="discrete-slider-restrict"
                    step={null}
                    valueLabelDisplay="auto"
                    marks={marks}
                  />
                </div>
              </div>
              <div className="box-btn">
                <div className="flex1">
                  <Button className="btn-cancel" onClick={handleThreshold}>
                    Cancel
                  </Button>
                </div>
                <div className="flex2">
                  <Button className="btn-submit" onClick={handleLoading}>
                    Submit
                  </Button>
                </div>
              </div>
            </Card>
          </div>
        </Fade>
      </Modal>

      <ModalLoading open={openLoading} progress={progress} />
      <ModalDone open={done} handleModalDone={handleModalDone} />
    </>
  );
}
const mapStateToProps = (state) => {
  return { ocr: state.ocr };
};
const mapDispatchToProps = (dispatch) => {
  return { postOCR: (data) => dispatch(postOCR(data)) };
};
export default connect(mapStateToProps, mapDispatchToProps)(ModalThreshold);
