import { API, requestBody } from "../config";

export const getUniversitas = () => {
  return async (dispatch) => {
    dispatch({
      type: "UNIVERSITAS_PENDING",
    });
    try {
      const res = await API.post("universitas/read", requestBody);
      dispatch({
        type: "UNIVERSITAS_FULFILLED",
        payload: res.data.data,
      });
    } catch (err) {
      dispatch({
        type: "UNIVERSITAS_REJECTED",
      });
    }
  };
};
