import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";

import { connect } from "react-redux";
import { getPelamar, getPelamarGagal } from "../../../../../../_action/pelamar";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    listStyle: "none",
    padding: theme.spacing(0.5),
    margin: 0,
  },
  chip: {
    margin: theme.spacing(0.5),
  },
}));

function ChipsArray(props) {
  const classes = useStyles();
  const { chip } = props.pelamar;
  const handleDelete = (chipToDelete) => async () => {
    const remainChip = chip.filter((data) => data.label !== chipToDelete.label);
    let newData = {};
    remainChip.map((data) => {
      console.log(data, "asw");
      newData = {
        ...newData,
        [data.name]: data.label,
      };
    });
    newData.status_screening = "ditolak";
    await props.getFilter(newData);
  };
  console.log(props.pelamar);
  return (
    <div component="ul" className={classes.root}>
      {chip.map((data) => {
        console.log(data, "ini chip");
        return (
          <li key={data.key}>
            <Chip
              label={data.label}
              className={classes.chip}
              onDelete={handleDelete(data)}
            />
          </li>
        );
      })}
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    pelamar: state.pelamar,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getFilter: (data) => dispatch(getPelamarGagal(data)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ChipsArray);
