const initState = {
  data: [],
  loading: true,
  error: null,
};

const universitas = (state = initState, action) => {
  switch (action.type) {
    case `UNIVERSITAS_PENDING`:
      return {
        ...state,
        loading: true,
      };

    case `UNIVERSITAS_FULFILLED`:
      return {
        ...state,
        data: action.payload,
        loading: false,
        error: false,
      };

    case `UNIVERSITAS_REJECTED`:
      return {
        ...state,
        loading: false,
        error: true,
      };
    default:
      return state;
  }
};

export default universitas;
