import React, { useState } from "react";

import useStyles from "./styles";
import home from "../asset/icons/home.svg";
import screening from "../asset/icons/screening.svg";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import { useHistory } from "react-router-dom";
import {
  List,
  ListItemIcon,
  ListItem,
  ListItemText,
  Collapse,
} from "@material-ui/core";

function Lists() {
  const classes = useStyles();
  const history = useHistory();
  const Home = () => <img src={home} alt="Sss" width="20px" />;
  const screen = () => (
    <img src={screening} alt="Sss" className={classes.homeicon} width="20px" />
  );

  const [action, setAction] = useState({
    open:false,
    childIndex: null,
    index:0
  });


  const listMenu = [
    { text: "Home ", route: "/", icon: Home, },
    { text: "Set an Event", route: "/event", icon: screen },
  
    {
      text: "Screening CV",
      icon: screen,
      children: [
        { text: "Verifying User", route: "/all-candidates", icon: screen },
        { text: "Failed User", route: "/user-gagal" },
      ],
    },
    { text: "Shortlisted", route: "/shortlisted", icon: screen },
  ];


  const handleClick = (param) => {
  
    setAction({ ...action, index: param.index });
    if (param.Value.children) {
      setAction({
        ...action,
        open: !(param.index === action.index && action.open),
        index: param.index,
        childIndex: null,
      });
    }
    history.push(param.Value.route);
  };

  const handleClickChild = (param) => {
    setAction({ ...action, childIndex: param.childIndex });
    history.push(param.key.route);
  };


  return (
    <div>
      <List component="nav">
        {listMenu.map((Value, index) => (
          <>
            <ListItem
              button
              selected={index === action.index}
              onClick={() => handleClick({index, Value})}
              className={classes.menulist}
            >
              
              <ListItemIcon>
                {Value.icon ? <Value.icon /> : <Value.icon2 />}
              </ListItemIcon>
              <ListItemText className={classes.menutext} primary={Value.text} />
              {Value.children && (
                <>
                  {action.open && index === action.index ? (
                    <ExpandLess  />
                  ) : (
                    <ExpandMore />
                  )}
                </>
              )}
            </ListItem>
            {Value.children && (
              <Collapse in={action.open && index === action.index} timeout="auto" unmountOnExit>
                {Value.children.map((key, childIndex) => {
                 
                  return (
                    <ListItem
                      button
                      selected={childIndex === action.childIndex}
                      onClick={() => handleClickChild({ childIndex, key })}
                      className={classes.menulist}
                    >
                      <ListItemIcon></ListItemIcon>

                      <ListItemText primary={key.text} />
                    </ListItem>
                  );
                })}
              </Collapse>
            )}
          </>
        ))}
      </List>
    </div>
  );
}

export default Lists;
