import { createStore, combineReducers, applyMiddleware } from "redux";
import auth from "../_reducer/auth";
import event from "../_reducer/event";
import jurusan from "../_reducer/jurusan";
import universitas from "../_reducer/universitas";
import pelamar from "../_reducer/pelamar";
import ocr from "../_reducer/ocr";
import thunk from "redux-thunk";

// Global state
const rootReducers = combineReducers({
  auth,
  event,
  jurusan,
  universitas,
  pelamar,
  ocr,
});

// Setup store for Redux
const store = createStore(rootReducers, applyMiddleware(thunk));

export default store;
