const initState = {
  data: [],
  authenticated: false,
  loading: true,
  error: null,
  isLogin: false,
  chip: [],
};

const pelamar = (state = initState, action) => {
  switch (action.type) {
    case `PELAMAR_PENDING`:
    case `PELAMARGAGAL_PENDING`:
    case `PELAMAR_SUKSES_PENDING`:
      return {
        ...state,
        loading: true,
      };

    case `PELAMAR_FULFILLED`:
    case `PELAMARGAGAL_FULFILLED`:
    case `PELAMAR_SUKSES_FULFILLED`:
      return {
        ...state,
        authenticated: true,
        data: action.payload,
        isLogin: true,
        loading: false,
        error: false,
        chip: action.chip,
      };

    case `PELAMAR_REJECTED`:
    case `PELAMARGAGAL_REJECTED`:
    case `PELAMAR_SUKSES_REJECTED`:
      return {
        ...state,
        loading: false,
        isLogin: false,
        error: true,
      };

    default:
      return state;
  }
};

export default pelamar;
