import React, { useState, useEffect } from "react";
import {
  Modal,
  Backdrop,
  Fade,
  CardContent,
  Card,
  Typography,
  TextField,
  Divider,
} from "@material-ui/core";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import useStyles from "../../styles";
import Autocomplete from "@material-ui/lab/Autocomplete";

import { connect } from "react-redux";
import { UpdateEvent } from "../../../../../_action/event";
import universitas from "../../../../../_reducer/universitas";

function Modalsetup(props) {
  const classes = useStyles();
  const { open, handleModal, index, dataJur, dataUni, dataModal } = props;
  const [update, setUpdate] = useState({
    universitas: {
      id: "",
      universitas: "",
    },
    jurusan: {
      id: "",
      jurusan: "",
    },
    ipk: "",
    ipk_2: "",
    umur: "",
    tanggal: "",
  });

  const handleUpdate = async (e) => {
    e.preventDefault();
    const id = dataModal.id;
    const universitas = update.universitas.id;
    const jurusan = update.jurusan.id;
    const ipk = update.ipk;
    const ipk_2 = update.ipk_2;
    const umur = update.umur;
    const status_requirement = "SET";
    const data = {
      universitas,
      jurusan,
      ipk,
      ipk_2,
      umur,
      status_requirement,
      id,
    };
    console.log(data, "woi");
    await props.UpdateEvent(data);
    handleModal("event");
  };

  const handleChange = (e) => {
    setUpdate({ ...update, [e.target.name]: e.target.value });
  };

  const handleChangeAutocomplete = (param) => (e, value) => {
    setUpdate({ ...update, [param]: value });
  };

  return (
    <>
      {dataJur.length > 0 && (
        <Modal
          className={classes.modal}
          open={open}
          closeAfterTransition
          BackdropComponent={Backdrop}
        >
          <Fade in={open}>
            <div>
              <Card className={classes.card} variant="outlined">
                <CardContent>
                  <div className={classes.close}>
                    <HighlightOffIcon
                      fontSize="large"
                      className="close-icon"
                      onClick={() => handleModal("event")}
                    />
                  </div>
                  <div className={classes.containerevent}>
                    <div className={classes.modalheader}>
                      <Typography>
                        <b>Setup requirements {dataJur.id} </b>
                      </Typography>
                      <Typography>
                        <b>event</b>
                      </Typography>
                    </div>

                    <div className={classes.formcntrl}>
                      <label>Universitas</label>
                      <Autocomplete
                        options={dataUni}
                        onChange={handleChangeAutocomplete("universitas")}
                        getOptionLabel={(option) => option.universitas}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            label=""
                            variant="outlined"
                            size="small"
                            name="universitas"
                            value={update.universitas}
                          />
                        )}
                      />
                    </div>
                    <div className={classes.formcntrl}>
                      <label>Jurusan</label>
                      <Autocomplete
                        id="combo-box-demo"
                        options={dataJur}
                        getOptionLabel={(option) => option.jurusan}
                        onChange={handleChangeAutocomplete("jurusan")}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            label=""
                            variant="outlined"
                            size="small"
                            name="jurusan"
                          />
                        )}
                      />
                    </div>
                    <div className={classes.formcntrl}>
                      <label>IPK</label>
                      <div className={classes.flx1}>
                        <TextField
                          style={{ width: 200 }}
                          variant="outlined"
                          size="small"
                          name="ipk"
                          onChange={handleChange}
                        />
                        <TextField
                          style={{ width: 200 }}
                          variant="outlined"
                          size="small"
                          name="ipk_2"
                          onChange={handleChange}
                        />
                      </div>
                    </div>
                    <div className={classes.formcntrl}>
                      <label>Umur</label>
                      <div className={classes.flx1}>
                        <TextField
                          variant="outlined"
                          size="small"
                          className="umur"
                          onChange={handleChange}
                        />
                        <Typography className={classes.umurtgl}>
                          Per tgl
                        </Typography>
                        <input type="date" className="date" />
                      </div>
                    </div>

                    <div className={classes.btncntrl}>
                      <div className={classes.flx1}>
                        <button className="btncancel" onClick={handleModal}>
                          Cancel
                        </button>
                        <button className="btnapply" onClick={handleUpdate}>
                          Apply
                        </button>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </Fade>
        </Modal>
      )}
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    event: state.event,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    UpdateEvent: (data) => dispatch(UpdateEvent(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Modalsetup);
