import React, { useEffect } from "react";

import shortlist from "./features/pages/shorlisted";
import Routing from "./features/pages/routing/route";
import Login from "./features/pages/login";
import PublicRoute from "./features/pages/routing/publicRouting";
import PrivateRoute from "./features/pages/routing/privateRouting";
import { BrowserRouter as Router, Switch, useHistory } from "react-router-dom";
import "./styles.app.css";
import { Verified } from "./_action/auth";
import { connect } from "react-redux";

function App(props) {
  const History = useHistory();
  useEffect(() => {
    props.Verified();
  }, []);

  const { authenticated, loading } = props.auth;

  return (
    <div className="App">
      <header className="App-header">
        <Switch>
          {" "}
          {!loading ? (
            authenticated ? (
              <PrivateRoute
                component={Routing}
                path="/"
                authenticated={authenticated}
              />
            ) : (
              History.push("/login")
            )
          ) : (
            <b>loading</b>
          )}
          <PublicRoute
            component={Login}
            exact
            path="/login"
            authenticated={authenticated}
          />
        </Switch>
      </header>
    </div>
  );
}

const mapStateToProps = (state) => {
  return { auth: state.auth };
};
const mapDispatchToProps = (dispatch) => {
  return { Verified: () => dispatch(Verified()) };
};
export default connect(mapStateToProps, mapDispatchToProps)(App);
