const initState = {
  data: [],
  loading: true,
  error: null,
};

const jurusan = (state = initState, action) => {
  switch (action.type) {
    case `JURUSAN_PENDING`:
      return {
        ...state,
        loading: true,
      };

    case `JURUSAN_FULFILLED`:
      return {
        ...state,
        data: action.payload,
        loading: false,
      };

    case `JURUSAN_REJECTED`:
      return {
        ...state,
        error: true,
      };

    default:
      return state;
  }
};

export default jurusan;
