const initState = {
  data: [],
  authenticated: false,
  loading: true,
  error: null,
  isLogin: false,
};

const auth = (state = initState, action) => {
  switch (action.type) {
    case `LOGIN_PENDING`:
      return {
        ...state,
        loading: true,
      };

    case `LOGIN_FULFILLED`:
      return {
        ...state,
        authenticated: true,
        data: action.payload,
        isLogin: true,
        loading: false,
        error: false,
      };

    case `LOGIN_REJECTED`:
      return {
        ...state,
        loading: false,
        isLogin: false,
        error: true,
      };

    case "LOGOUT":
      return {
        ...state,
        authenticated: false,
        user: null,
        loading: false,
        error: null,
      };
    case `TOKEN_VERIFIED`:
      return {
        ...state,
        authenticated: true,
        loading: false,
      };
    case `TOKEN_UNVERIFIED`:
      return {
        ...state,
        authenticated: false,
        loading: false,
      };

    default:
      return state;
  }
};

export default auth;
