import { API } from "../config";

export const Verified = (data) => {
  return (dispatch) => {
    const token = localStorage.getItem("token");
    if (!!token) {
      dispatch({
        type: "TOKEN_VERIFIED",
        payload: true,
      });
    } else {
      dispatch({
        type: "TOKEN_UNVERIFIED",
        payload: false,
      });
    }
  };
};

export const signIn = (data) => {
  return async (dispatch) => {
    dispatch({
      type: "LOGIN_PENDING",
    });
    try {
      const res = await API.post("/fire/login", data);
      localStorage.setItem("token", res.data.data.token);
      localStorage.setItem("username", res.data.data.nama);

      dispatch({
        type: "LOGIN_FULFILLED",
        payload: res.data,
      });
    } catch (err) {
      dispatch({
        type: "LOGIN_REJECTED",
      });
    }
  };
};
