import React, { useEffect } from "react";
import useStyles from "../../../styles";
import { Autocomplete } from "@material-ui/lab";
import { Typography, TextField } from "@material-ui/core";

import { getJurusan } from "../../../../../../_action/jurusan";
import { getUniversitas } from "../../../../../../_action/universitas";
import { getPelamar, getPelamarGagal } from "../../../../../../_action/pelamar";

import { connect } from "react-redux";

function Search(props) {
  useEffect(() => {
    props.getPelamarGagal();
    props.getUniversitas();
    props.getJurusan();
  }, []);

  const [filter, setFilter] = React.useState({
    universitas: "",
    jurusan: "",
    ipk: "",
    umur: "",
  });

  console.log(props.pelamar.chip, "ini chipnya tuan");
  const handleFilter = async (e) => {
    e.preventDefault();
    const universitas = filter.universitas;
    const jurusan = filter.jurusan;
    const ipk = filter.ipk;
    const umur = filter.umur;
    const status_screening = "ditolak";
    const data = {
      universitas,
      jurusan,
      ipk,
      umur,
      status_screening,
    };
    console.log(data);
    for (let prop in data) {
      if (!data[prop]) delete data[prop];
    }
    await props.getFilter(data);
  };

  const { universitas, jurusan } = props;

  const handleChange = (e) => {
    setFilter({ ...filter, [e.target.name]: e.target.value });
  };

  const classes = useStyles();
  return (
    <div>
      <div className={classes.formcntrl}>
        <label>Universitas</label>
        <Autocomplete
          options={universitas?.data?.map((option) => option.universitas)}
          onChange={(e, value) => {
            console.log(value, "ini valuenya ");
            setFilter({ ...filter, universitas: value });
          }}
          renderInput={(params) => (
            <TextField {...params} label="" variant="outlined" size="small" />
          )}
          value={filter.universitas}
        />
      </div>
      <div className={classes.formcntrl}>
        <label>Jurusan</label>
        <Autocomplete
          options={jurusan?.data?.map((option) => option.jurusan)}
          onChange={(e, value) => {
            setFilter({ ...filter, jurusan: value });
          }}
          renderInput={(params) => (
            <TextField {...params} label="" variant="outlined" size="small" />
          )}
          value={filter.jurusan}
        />
      </div>
      <div className={classes.formcntrl}>
        <label>IPK</label>
        <div className={classes.flx1}>
          <TextField
            variant="outlined"
            size="small"
            className="ipk"
            onChange={handleChange}
            name="ipk"
          />
          <TextField variant="outlined" size="small" className="ipk" disabled />
        </div>
      </div>
      <div className={classes.formcntrl}>
        <label>Umur</label>
        <div className={classes.flx1}>
          <TextField
            label=""
            variant="outlined"
            size="small"
            className="umur"
            name="umur"
            onChange={handleChange}
          />

          <Typography className={classes.umurtgl}>Per tgl</Typography>
          <input type="date" className="date" />
        </div>
      </div>

      <div className={classes.btncntrl}>
        <button className="btnapply" onClick={handleFilter}>
          Apply
        </button>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    universitas: state.universitas,
    jurusan: state.jurusan,
    pelamar: state.pelamar,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getFilter: (data) => dispatch(getPelamarGagal(data)),
    getPelamarGagal: () => dispatch(getPelamarGagal()),
    getJurusan: () => dispatch(getJurusan()),
    getUniversitas: () => dispatch(getUniversitas()),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Search);
