import React, { useEffect, useState } from "react";
import useStyle from "./styles";
import Banner from "../../asset/gif/hero-motiologin.gif";
import { Typography, Card, FormControlLabel, Radio } from "@material-ui/core";
import Logo from "../../asset/icons/ocrlogos.svg";
import google from "../../asset/icons/google.svg";
import { connect } from "react-redux";
import { signIn } from "../../../_action/auth";

function Login(props) {
  const classes = useStyle();
  const [login, setLogin] = useState({ username: "", password: "" });

  const handleLogin = async (e) => {
    e.preventDefault();
    // const username = login.username;
    // const password = login.password;
    // const data = { username, password };

    await props.signIn(login);

    props.history.push("/");
  };
  const handleChange = (e) => {
    setLogin({ ...login, [e.target.name]: e.target.value });
  };

  return (
    <div className={classes.container}>
      <div className={classes.container1}>
        <div className={classes.flx1}>
          <img src={Banner} alt="sssss" width="972px" height="500px" />
          <Typography className={classes.textdesc}>
            made with &#10084;&#65039; by DSC Team
          </Typography>
          <Typography className={classes.textdesc2}>
            &copy; 2020 All Rights Reserved
          </Typography>
        </div>
        <div className={classes.flx2}>
          <Card className={classes.card}>
            <div className={classes.formcntrl3}>
              <img src={Logo} alt="logo" width="170px" />
            </div>
            <div className={classes.formcntrl}>
              <label>Email</label>
              <input
                className={classes.textinput}
                onChange={handleChange}
                name="username"
                type="text"
              />
            </div>
            <div className={classes.formcntrl}>
              <label>Password</label>
              <input
                className={classes.textinput}
                onChange={handleChange}
                name="password"
                type="password"
              />
            </div>
            <div className={classes.formcntrl3}>
              <div>
                <FormControlLabel
                  value="end"
                  control={<Radio color="primary" size="small" />}
                  label={
                    <Typography className={classes.remember}>
                      remember me
                    </Typography>
                  }
                />
                {/* <Radio color="primary" size="small" />
                    <label style={{fontSize:"12px"}}></label> */}
              </div>
              <div>
                <label style={{ fontSize: "12px" }}>Forgot password?</label>
              </div>
            </div>
            <div className={classes.formcntrl}>
              <button className={classes.btnlogin} onClick={handleLogin}>
                Login now
              </button>
            </div>
            <div className={classes.formcntrl}>
              <button className={classes.btnlogin2}>
                {" "}
                <img src={google} alt="a" /> &nbsp; Or sign-in with google
              </button>
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
}
const mapStateToProps = (state) => {
  return { auth: state.auth };
};
const mapDispatchToProps = (dispatch) => {
  return { signIn: (data) => dispatch(signIn(data)) };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
