import React, { useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import {
  Table,
  Paper,
  TableRow,
  TableContainer,
  TableBody,
  IconButton,
  Typography,
  TableCell,
  TableHead,
  Divider,
  Button,
} from "@material-ui/core";
import useStyles from "../../styles";
import AutorenewIcon from "@material-ui/icons/Autorenew";
// import data from "../../../../../dummydata/data.json";

import Ijazah from "../modal/modalUploadIjazah";
import Ktp from "../modal/modalUploadKtp";

import Chip from "../filter/chip";
import {
  getPelamar,
  getPelamarGagal,
  getPelamarSukses,
} from "../../../../../_action/pelamar";
import { connect } from "react-redux";

const StyledTableCell = withStyles((theme) => ({
  head: {
    color: "black",
    fontWeight: "bold",
    fontSize: 12,
  },
  body: {
    fontSize: 13,
    color: "#808CA0",
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: "white",
      border: "2px solid white",
      fontSize: "14px",
    },
  },
}))(TableRow);

function TablesAllcandidates(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState({
    ktp: false,
    ijazah: false,
  });
  const [check, setCheck] = React.useState(false);
  const [checkone, setCheckone] = React.useState(false);
  const [threshold, setThreshold] = React.useState(false);

  const handleCheck = () => {
    setCheck(!check);
  };
  const handleCheckOne = () => {
    setCheckone(true);
  };

  const handleModal = (param) => {
    {
      param === "ktp"
        ? setOpen({ ...open, ktp: !open.ktp })
        : setOpen({ ...open, ijazah: !open.ijazah });
    }
  };
  const handleThreshold = () => {
    setThreshold(!threshold);
  };

  useEffect(() => {
    props.getPelamarSukses();
  }, []);

  const { data } = props.pelamar;
  return (
    <div>
      <div className="activefilter">
        <div>
          <Typography>Filter yang aktif :</Typography>
          <Chip />
        </div>
        <div>
          <IconButton className="btnrefresh">
            <AutorenewIcon style={{ color: "white" }} />
          </IconButton>
        </div>
      </div>
      <div style={{ overflowX: "scroll", width: "870px" }}>
        <TableContainer component={Paper} className={classes.paper}>
          <Table className={classes.table}>
            <TableHead>
              <TableRow style={{ padding: "10px" }}>
                <StyledTableCell align="center" width="50px">
                  <input type="checkbox" onClick={handleCheck} />
                </StyledTableCell>
                <StyledTableCell align="center" width="50px">
                  No
                </StyledTableCell>
                <StyledTableCell align="Left" width="200px">
                  NAMA
                </StyledTableCell>
                <StyledTableCell align="Left" width="200px">
                  NIK
                </StyledTableCell>
                <StyledTableCell align="center" width="100px">
                  UMUR
                </StyledTableCell>
                <StyledTableCell align="center" width="200px">
                  TEMPAT/TGL LAHIR
                </StyledTableCell>
                <StyledTableCell align="center" width="100px">
                  UNIVERSITAS
                </StyledTableCell>
                <StyledTableCell align="center" width="150px">
                  JURUSAN
                </StyledTableCell>
                <StyledTableCell align="center" width="150px">
                  IPK
                </StyledTableCell>
                <StyledTableCell align="center" width="200px">
                  Status Upload KTP
                </StyledTableCell>
                <StyledTableCell align="center" width="200px">
                  Status Upload IJAZAH
                </StyledTableCell>
                <StyledTableCell align="center" width="200px">
                  EVENT/POSISI
                </StyledTableCell>
                <StyledTableCell align="center" width="200px">
                  STATUS SHORTLISTED
                </StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <Divider />
              {data.map((row, index) => (
                <>
                  {row.status_screening === "diterima" && (
                    <StyledTableRow>
                      <StyledTableCell align="center">
                        {check ? (
                          <input type="checkbox" checked />
                        ) : (
                          <input type="checkbox" onClick={handleCheckOne} />
                        )}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        {index + 1}
                      </StyledTableCell>
                      <StyledTableCell>{row.nama_pelamar}</StyledTableCell>
                      <StyledTableCell>{row.nik}</StyledTableCell>
                      <StyledTableCell align="center">
                        {row.umur}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        {row.tempat_lahir}, {row.tanggal_lahir}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        {row.universitas}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        {row.jurusan}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        {row.ipk} - {row.range}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        <Button
                          className={classes.btnUploaded}
                          onClick={() => handleModal("ktp")}
                        >
                          Uploaded
                        </Button>
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        <Button
                          className={classes.btnUploaded}
                          onClick={() => handleModal("ijazah")}
                        >
                          Uploaded
                        </Button>
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        {row.open_position}
                      </StyledTableCell>
                      <StyledTableCell align="center">
                        <label className={classes.statusSL}>Shortlisted</label>
                      </StyledTableCell>
                    </StyledTableRow>
                  )}
                </>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>

      {/* =============================================================================================================================== */}
      <Ijazah
        handleModal={() => handleModal("ijazah")}
        open={open.ijazah}
        id={data.id}
      />

      <Ktp
        handleModal={() => handleModal("ktp")}
        open={open.ktp}
        id={data.id}
      />
    </div>
  );
}

const mapStateToProps = (state) => {
  return { pelamar: state.pelamar };
};
const mapDispatchToProps = (dispatch) => {
  return { getPelamarSukses: () => dispatch(getPelamarSukses()) };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TablesAllcandidates);
