import React, { useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Divider,
} from "@material-ui/core";
import Editcalender from "../../../../asset/icons/calendar_edit.svg";
import useStyles from "../../styles";
import Modalevent from "../modal/modalevent";
import ModalProcess from "../modal/modalprocess";
import { getJurusan } from "../../../../../_action/jurusan";
import { getUniversitas } from "../../../../../_action/universitas";
import { connect } from "react-redux";
import { getEvent } from "../../../../../_action/event";

const StyledTableCell = withStyles((theme) => ({
  head: {
    color: "black",
    fontWeight: "bold",
  },
  body: {
    fontSize: 13,
    color: "#808CA0",
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: "white",
      border: "2px solid white",
      fontSize: "14px",
    },
  },
}))(TableRow);

function TableEvent(props) {
  useEffect(() => {
    props.getEvent();
    props.getJurusan();
    props.getUniversitas();
  }, []);

  const classes = useStyles();

  const [openIndx, setOpenIndx] = React.useState({
    modal: false,
    modal2: false,
  });
  const [dataModal, setDataModal] = React.useState({});
  const [mustUpdate, setMustUpdate] = React.useState(false);

  useEffect(() => {
    if (mustUpdate) {
      props.getEvent();
      props.getJurusan();
      props.getUniversitas();
    }
  }, [mustUpdate]);

  const handleModal = (param, row) => {
    setDataModal(row);
    console.log(row);
    param === "event"
      ? setOpenIndx({ ...openIndx, modal: !openIndx.modal })
      : setOpenIndx({ ...openIndx, modal2: !openIndx.modal2 });
    if (!row) {
      setMustUpdate(true);
    } else {
      setMustUpdate(false);
    }
  };

  const { data } = props.event;
  const { jurusan, universitas } = props;
  console.log(props, "data");
  if (!data) return <b>loading</b>;
  return (
    <>
      <TableContainer component={Paper} className={classes.paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow style={{ padding: "10px" }}>
              <StyledTableCell align="center" width="100px">
                No
              </StyledTableCell>
              <StyledTableCell>Open Position</StyledTableCell>
              <StyledTableCell align="center">
                Status Requirements
              </StyledTableCell>
              <StyledTableCell align="center">
                Status Requirements
              </StyledTableCell>
              <StyledTableCell align="center">
                Requirements setup
              </StyledTableCell>
              <StyledTableCell align="center">Recruitment step</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <Divider />
            {data.map((row, index) => (
              <StyledTableRow>
                <StyledTableCell align="center">{row.id}</StyledTableCell>
                <StyledTableCell>{row.open_position}</StyledTableCell>
                <StyledTableCell align="center">
                  <div
                    className={
                      row.status_requirement === "UNSET"
                        ? classes.status
                        : classes.status2
                    }
                  >
                    {row.status_requirement}
                  </div>
                </StyledTableCell>
                <StyledTableCell align="center">
                  <div
                    className={
                      row.status_recruitment === "UNSET"
                        ? classes.status
                        : classes.status2
                    }
                  >
                    {row.status_recruitment}
                  </div>
                </StyledTableCell>
                <StyledTableCell align="center">
                  <div className={classes.flexedit}>
                    <img
                      className={classes.edit}
                      src={Editcalender}
                      alt="edit"
                    />
                    &nbsp;&nbsp;
                    <button
                      type="button"
                      className={classes.buttonedit}
                      onClick={() => handleModal("event", row)}
                    >
                      edit action
                    </button>
                  </div>
                </StyledTableCell>
                <StyledTableCell align="center">
                  <div className={classes.flexedit}>
                    <img
                      className={classes.edit}
                      src={Editcalender}
                      alt="edit"
                    />
                    &nbsp;&nbsp;
                    <button
                      type="button"
                      className={classes.buttonedit}
                      onClick={() => handleModal("process", row)}
                    >
                      edit action
                    </button>
                  </div>
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div className={classes.pagination}>
        <div className="datapage">Showing 15 from 100 data</div>
        <div className="formpagination">
          <div className="boxnumber">
            <div className="number">
              <button className="btnnumber">1</button>
            </div>
            <div className="number">
              <button className="btnnumber">2</button>
            </div>
            <div className="number">
              <button className="btnnumber">3</button>
            </div>
            <div className="number">
              <button className="next">next</button>
            </div>
          </div>
        </div>
      </div>
      <ModalProcess
        handleModal={handleModal}
        open={openIndx.modal2}
        datas={data}
        dataModal={dataModal}
      />
      <Modalevent
        handleModal={handleModal}
        open={openIndx.modal}
        dataJur={jurusan.data}
        dataUni={universitas.data}
        dataModal={dataModal}
      />
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    event: state.event,
    jurusan: state.jurusan,
    universitas: state.universitas,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getEvent: () => dispatch(getEvent()),
    getUniversitas: () => dispatch(getUniversitas()),
    getJurusan: () => dispatch(getJurusan()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TableEvent);
